@font-face {
  font-family: Monsterrat;
  src: url(../fonts/Montserrat-Regular.ttf);
}
@font-face {
  font-family: Monsterrat-Light;
  src: url(../fonts/Montserrat-Light.ttf);
}
@font-face {
  font-family: Monsterrat-Bold;
  src: url(../fonts/Montserrat-Bold.ttf);
}
a:link, a:visited {
  color: inherit;
  text-decoration: none;
}

::-webkit-scrollbar {
  display: none;
}

html {
  scroll-behavior: smooth;
}
.html-block {
  scroll-behavior: smooth;
  overflow: hidden;
}

.bg-img {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.main-nav {
  display: flex;
  position: fixed;
  z-index: 1;
  width: 95%;
  margin: 2vw 0 0 2vw;
  padding: 0 1vw;
  gap: 2vw;
  justify-content: space-between;
  background-color: rgba(72, 73, 107, 0.4235294118);
  border-radius: 2vw;
}
.main-nav .nav-logo {
  width: 15vw;
}
.main-nav .nav-content-container {
  display: flex;
  align-items: center;
  gap: 5vw;
}
.main-nav .nav-content-container .nav-content {
  color: white;
  font-size: 1.7vw;
  font-family: Monsterrat-Light;
  font-weight: 100;
  cursor: pointer;
  transition: 0.3s;
}
.main-nav .nav-content-container .nav-content:hover {
  color: rgb(212, 212, 212);
}

.search-container2 {
  width: 20vw;
  display: flex;
  align-items: flex-end;
  padding-bottom: 2vw;
}
.search-input {
  background-color: transparent;
  border: none;
  border-bottom: 0.3vw solid rgb(35, 34, 56);
  width: 100%;
  font-size: 1.5vw;
  color: white;
  font-family: Monsterrat;
  padding: 0.5vw 0 0.5vw 3vw;
  outline: none;
}
.search-input::placeholder {
  font-size: 1.5vw;
  color:rgb(109, 107, 107);
}
.search_icon {
  position: absolute;
  margin: 0 0 0.5vw 0.5vw;
  width: 2vw;
  filter: brightness(10%);
  transition: 0.5s;
}
.search-container2:active .search_icon {
  transform: scale(0.7);
}


.search-resault-container {
  margin-top: 10vw;
  margin-bottom: -5vw;
  margin: 10vw 3vw -5vw 3vw;
  border-radius: 2vw;
  padding: 2vw;
  color: white;
  font-family: Monsterrat;
  display: flex;
  flex-wrap: wrap;
  background-color: #3c375886;
}
.search-resault-container2 {
  display: flex;
  flex-wrap: wrap;
  gap: 5vw;
  justify-content: center;
}
.search-car-img {
  width: 20vw;
  border-radius: 2vw;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.479);
  cursor: pointer;
  transition: 0.3s;
}
.search-car-img:hover {
  filter: brightness(80%);
  transform: scale(0.99);
}
.single-car22 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.search-res-h1 {
  font-size: 2vw;
}
.search-car-h3 {
  font-size: 1.3vw;
}






.main-header {
  margin: 18vw 0 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10vw;
}
.main-header iframe {
  width: 50%;
  height: 20vw;
}
.main-header .header-h1 {
  color: white;
  font-size: 3.5vw;
  font-family: Monsterrat;
  font-weight: 500;
  text-align: right;
}
.main-header .header-h1 .header-span {
  font-family: Monsterrat-Bold;
  font-weight: 300;
  font-size: 4.5vw;
}

.main-content {
  margin: 10vw 0 0 0;
  position: relative;
  width: 100%;
}
.main-content .about-us .aboutus-video {
  position: absolute;
  width: 100%;
  height: auto;
  z-index: -1;
  filter: brightness(60%);
}
.main-content .about-us .aboutus-h1 {
  padding: 5vw 0 5vw 0;
  color: white;
  font-family: Monsterrat-Bold;
  text-align: center;
  font-size: 3vw;
  text-shadow: 0 4px 10px rgba(0, 0, 0, 0.438);
}
.main-content .about-us .aboutus-p {
  margin: 0 auto 17vw auto;
  width: 70%;
  color: white;
  font-family: Monsterrat;
  text-align: center;
  font-size: 2vw;
}
.main-content .about-us .aboutus-p-span {
  font-family: Monsterrat-Bold;
  font-weight: bold;
}
.main-content .garage .garage-bg {
  position: absolute;
  width: 100%;
  z-index: -1;
  filter: brightness(90%);
}
.main-content .garage .garage-h1 {
  color: white;
  font-size: 3vw;
  padding: 5vw 0 0 0;
  text-align: center;
  font-family: Monsterrat-Light;
  font-weight: bold;
  letter-spacing: 3px;
}
.main-content .garage .garage-iframe-container {
  display: flex;
  flex-direction: column;
  padding: 5vw 0 10vw 0;
  gap: 3vw;
}
.main-content .garage .garage-iframe-container .gar-ifr-cont-1-3 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 3vw;
}
.main-content .garage .garage-iframe-container .gar-ifr-cont-1-3 iframe, .main-content .garage .garage-iframe-container .gar-ifr-cont-1-3 .more-cars-container {
  width: 20vw;
  height: 15vw;
  box-shadow: 4px -4px 10px rgba(0, 0, 0, 0.432);
}
.main-content .garage .garage-iframe-container .gar-ifr-cont-1-3 .more-cars-container {
  position: relative;
  overflow: hidden;
}
.main-content .garage .garage-iframe-container .gar-ifr-cont-1-3 .more-cars-container .more-cars {
  width: 25vw;
  margin-left: -2vw;
  transition: 0.3s;
  cursor: pointer;
}
.main-content .garage .garage-iframe-container .gar-ifr-cont-1-3 .more-cars-container .more-cars:hover {
  width: 30vw;
  margin-top: -2vw;
}
.main-content .garage .garage-iframe-container .gar-ifr-cont-1-3 .more-cars-container .more-cars-caption {
  position: absolute;
  z-index: 1;
  color: white;
  left: 30%;
  top: 30%;
  font-family: Monsterrat-Light;
  font-weight: bold;
  letter-spacing: 3px;
  font-size: 1.8vw;
}

.contacts {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15vw;
  background-color: rgba(72, 73, 107, 0.5960784314);
  width: 90%;
  border-radius: 5vw;
  margin: 0 auto 10vw auto;
}
.contacts .canvas-container {
  width: 30% !important;
  height: 30vw !important;
}
.contacts form {
  display: flex;
  flex-direction: column;
  gap: 2.5vw;
  width: 40%;
  margin: 0 0 10vw 0;
}
.contacts form h1 {
  color: white;
  font-size: 3vw;
  margin: 7vw 0 4vw 0;
  font-family: Monsterrat-Light;
  font-weight: bold;
}
.contacts form input, .contacts form select {
  color: #b4b4b4;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.479);
  background-color: #4B485C;
  padding: 1vw 0 1vw 2vw;
  border-radius: 1.4vw;
  outline: none;
  border: none;
  font-size: 1.5vw;
  font-family: Monsterrat-Light;
}
.contacts form input::-webkit-input-placeholder {
  color: #b4b4b4;
}
.contacts form input::-moz-placeholder {
  color: #b4b4b4;
}
.contacts form input[type=submit] {
  background-color: #5847b4;
  color: white;
  margin: 0 0 0 0;
  width: 80%;
  padding: 1vw 0;
  transition: 0.3s;
  cursor: pointer;
}
.contacts form input[type=submit]:hover {
  background-color: #4b3b9e;
  color: rgb(211, 210, 210);
}
.contacts form input[type=submit]:active {
  background-color: #251866;
  color: rgb(182, 182, 182);
}
.div4submit {
  display: flex;
  justify-content: flex-end;
}

.conter-component {
  color: white;
}

.main-footer {
  width: 100%;
  background-color: #635E6C;
  color: white;
  font-size: 1.2vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-family: Monsterrat;
  margin: 0 auto;
}
.main-footer .footer-p {
  width: 80%;
  margin: 5vw auto 0 auto;
  text-align: center;
}
.main-footer .author {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5vw auto 2vw auto;
  gap: 10vw;
}
.main-footer .author .socials img {
  width: 3vw;
  height: 3vw;
}
.main-footer .hr-footer {
  width: 80%;
  height: 2px;
  background-color: #D9D9D9;
}
.main-footer .footer-copyright {
  margin-left: 10%;
}/*# sourceMappingURL=style.css.map */



/* custom hooks */

.customHooks {
  color: white;
  font-size: 1.5vw;
  font-family: Monsterrat;
}

.name-email-container {
  display: flex;
  flex-direction: column;
  gap: 2vw;
  width: 100%;
}
.auto-fill-input {
  background-color: #939096 !important;
  color: rgb(46, 46, 73) !important;
  border-radius: 1vw !important;
  padding: 0.6vw 0.3vw !important;
  width: 60%;
  text-align: center;
  font-size: 1.2vw !important;
  transition: 0.5s;
  cursor: pointer;
  font-weight:bold;
  margin-bottom: 1vw;
}
.auto-fill-input:hover {
  background-color: #666169 !important;
  color: rgb(202, 202, 206) !important;
}